import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/components/MainPage'
import Login from '@/components/Login'
import Menu from '@/components/Menu'
import Forums from '@/components/Forums'
import Articles from '@/components/Articles'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: {
        name: "Login"
      }
    },

    {
      path: '/Login',
      name: 'Login',
      component: Login
       },
    {
      path: '/MainPage',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/Menu',
      name: 'Menu',
      component: Menu
    },
    {
      path: '/Forums',
      name: 'Forums',
      component: Forums
    },
    {
      path: '/Articles',
      name: 'Articles',
      component: Articles
    }
  ]
})



