<template>
 <div id="fullpage">
   <div id="sidebar">
            <div id="menuhead">
                <h3> Navegación </h3>
            </div>
            <div id="menu-buttons">
                <a @click="navInicio"> <div class="element"> Inicio </div></a>
                <a @click="navForums"> <div class="element"> Foros </div></a>
                <a @click="navArticles"> <div class="element"> Artículos </div></a>
            </div>

    </div>
   <div id="content">
     <iframe width="100%" height="100%" :src="this.source"></iframe>
   </div>
  </div>
</template>

<script>
export default {
  name: 'MenuPrincipal',

  data: function() {
        return {
            source:'http://localhost:8080/#/MainPage',
        }
    },
  methods: {

      navInicio () {
      this.source='http://localhost:8080/#/MainPage';
    },
      navForums() {
      this.source='http://localhost:8080/#/Forums';
  },
    navArticles() {
    this.source='http://localhost:8080/#/Articles';
  },


  }
}
</script>

<style scoped>

*{
  padding: 0;
  margin: 0;
}

#fullpage{
  display: flex;
}

#sidebar{
  background: #f7f7f7;
  height: 100vh;
  width: 22%;
}

#content{
  background: rgba(33,52,133,0.20);
  width: 100%;
  height: 100vh;
}

#menuhead{
  background: #4C8BF5;
  padding: 1em;
}

h3{
  text-align: center;
  font-size: 2em;
  font-family:  "GOTY0","GOTY1","GOTY2",verdana;
  color: #f7f7f7;
}

#sidebar div{
  text-align: center;
  padding: 20px;
  border-bottom: solid 1px #7a7a7a;
  font-size: 16px;
  font-family:  "GOTY0","GOTY1","GOTY2",verdana;
}

#menu-buttons {
  max-height: 800px;
  overflow: scroll;
  font-weight: bold;
}

#sidebar .element:hover{
  background: rgba(14,44,164,0.30);
  color: white;
  font-weight: bold;
}

.inputForm {
  width: 90%;
  clear: both;
  color: #213485;
  margin: 3%;
  font-size: 20px;
  font-family: Verdana;
  font-size: 20px;
}

.inputForm  input {
  width: 100%;
  clear: both;
  margin-top: 2%;
  margin-bottom: 5%;
  height: 50px;
  font-size: 20px;
  font-family: "GOTY0", "GOTY1", "GOTY2", verdana;
  opacity: 50%;
  border-radius: 6px;
  border: transparent;
  background: #f2f2f2;
  padding: 10px;
}

.inputForm  textarea {
  padding: 10px;
  width: 150%;
  height: 90px;
  color: #213485;
  margin-top: 2%;
  margin-bottom: 0%;
  font-family: "GOTY0", "GOTY1", "GOTY2", verdana;
  opacity: 50%;
  border-radius: 6px;
  border: transparent;
  background: #f2f2f2;
  width: 100%;
  font-family: Verdana;
  font-size: 20px;
}

button{
  margin-top: 0%;
  margin-left: 3%;
  color: #0E2CA4;
  opacity: 70%;
  font-family: "GOTY0", "GOTY1", "GOTY2", verdana;
  text-shadow: 1px 1px rgba(14,44,164,0.50);
  background-color: transparent;
  padding: 5px;
  font-weight: 700;
  font-size: 24px;
  border-radius: 6px;
  border: transparent;
  margin-bottom: 40px;
}

button:hover{
  background-color: rgba(14,44,164,0.30) ;
}

#test{
  background-color: rgba(33,52,133,0.20);
  margin: 1%;
  color: #3B0EA4;
  font-family: "GOTY0", "GOTY1", "GOTY2", verdana;
}

#header1{
  margin: 2%;
  font-family: Verdana;
  font-size: 60px;
  color: #3B0EA4;
  text-align: center;
}

#table{
  width: 80%;
  margin-left: 10%;
  margin-top: 2%;
}

label{
  font-family: Verdana;
  font-weight: bold;
}

#card{
  background: #fff;
  width: 80%;
  margin: 5em;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.14);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.14);
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.14);
  border-radius: 15px;
}

#cardheader{
  height: 20px;
  width: 100%;
  background: #3B0EA4;
}

#buttons{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>



