<template>
  <div id="app">
    <router-view/>
    <img src="@/assets/logo.png">
    <form>
      <label>Usuario</label>
      <br>
      <input placeholder="Nombre de Usuario">
      <br>
      <label>Contraseña</label>
      <br>
      <input  placeholder="Contraseña">

  </form>

   <button @click="Login"> Iniciar Sesión </button>
</div>
</template>

<script>
export default {
  name: 'Login',
  methods: {
      Login () {
      this.$router.push({ name:'Menu' });
    }
}
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.inputForm {
  width: 400px;
  clear: both;
  color: #213485;
  margin: 3%;

}

  #app  input {
  width: 30%;
  clear: both;
  margin-top: 2%;
  margin-bottom: 5%;
  font-family: "GOTY0", "GOTY1", "GOTY2", verdana;
  opacity: 50%;
  border-radius: 6px;
  border: transparent;
  text-align: center;
}

  #app  button {
    margin-top: 0%;
    margin-left: 0%;
    color: #0E2CA4;
    opacity: 70%;
    font-family: "GOTY0", "GOTY1", "GOTY2", verdana;
    text-shadow: 1px 1px rgba(14,44,164,0.50);
    background-color: transparent;
    padding: 5px;
    font-weight: 700;
    font-size: 12px;
    border-radius: 6px;
    border: transparent;
    text-align: center;

    top: 50%;
  }

  #app  button:hover {
    background-color: rgba(14,44,164,0.30) ;
  }


</style>



